.details {
  &__container {
    display: grid;
    row-gap: 40px;

    @include onTablet {
      row-gap: 48px;
    }
  }
}

.detail {
  &__images {
    @include pageGrid;
    margin-bottom: 16px;
  }

  &__link {
    position: relative;

    &--wide {
      @include onTablet {
        grid-column: span 4;
      }
      @include onDesktop {
        grid-column: span 8;
      }
    }
    &--short {
      padding-bottom: 100%;

      @include onTablet {
        grid-column: span 2;
      }
      @include onDesktop {
        grid-column: span 4;
      }
    }

    @include onDesktop {
      @include hover(transform, scale(1.05));
    }
  }

  &__image {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__title {
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
  }

  &__description {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
  }
}
