.section-title {
  margin-bottom: 48px;
  font-size: 32px;
  font-style: normal;
  font-weight: 500;
  line-height: 100%;
  letter-spacing: -1.5px;
  text-align: center;
  color: #fff;

  @include onTablet {
    margin-bottom: 64px;
    font-size: 48px;
    line-height: 48px;
  }

  @include onDesktop {
    margin-bottom: 48px;
    font-size: 64px;
    line-height: 64px;
  }

  &--align-left {
    text-align: left;
  }

}
